@use "variable" as v;
@use "mixin" as m;

html,
body {
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;//スマホのカクカク回避
  touch-action: manipulation;//スマホのタッチを早くする
  scroll-behavior: smooth;//スムーススクロール
}

// 固定ページのbody
body.topic {
  overflow: hidden;
}

html {
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-weight: v.$font-weight--base;
  font-size: 62.5%;

  @media screen and (min-width: 460px) {
    font-family: 'M PLUS Rounded 1c', sans-serif;
  }

  // 背景で静止している星
  &::before {
    box-shadow: v.$box-shadow--star-white;
  }
  &::after { 
    box-shadow: v.$box-shadow--star-pink;
  }
}


h2 {
  font-weight: v.$font-weight--h2;
}
h3 {
  font-weight: v.$font-weight--h3;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

#waveCanvas {
  position: absolute;
  bottom: 0;
}

// 背景で静止している星
html::before,
html::after,
body::before,
body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
}


// 背景で静止している星
body {
  &::before {
    box-shadow: v.$box-shadow--star-purple;
  }

  &::after {
    box-shadow: v.$box-shadow--star-green;
  }
}


